.hero {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;

  header & {
    display: flex;
    align-items: center;
  }
  @media screen and (max-width: 766px) {
    width: 480px;
    margin: 0 auto;
  }

  @media (min-width: 767px) and (max-width: 1365px) {
    width: 768px;
    height: 384px;
    padding-right: 40px;
    padding-left: 40px;
  }

  @media screen and (min-width: 1366px) {
    width: 1366px;
    height: 718px;
  }
}
.hero__title {
  font-family: $primary_FontFamily;
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03rem;
  max-width: 280px;

  color: var(--primary-text-color);

  margin-top: 50px;
  margin-bottom: 0;
  padding-bottom: 30px;

  @media (min-width: 767px) and (max-width: 1365px) {
    font-size: 26px;
    line-height: 1.3;
    max-width: 332px;

    padding-bottom: 65px;
    margin-top: 70px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 58px;
    line-height: 1.29;
    max-width: 660px;

    padding-bottom: 80px;
    margin-top: 125px;
  }
}

.hero__text {
  font-family: $primary_FontFamily;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.57;
  letter-spacing: 0.08rem;

  color: var(--header-menu-text-color);

  max-width: 280px;
  margin-top: 30px;
  margin-bottom: 0;

  @media screen and (min-width: 1366px) {
    font-size: 20px;
    line-height: 1.65;
    max-width: 493px;

    margin-top: 24px;
    margin-bottom: 0;
  }
}
.hero__text_black {
  color: var(--primary-text-color);
  font-weight: 800;
}
.hero__text_orange {
  color: var(--all-orange-color);
  font-weight: 800;
}

.hero__picture {
  position: absolute;
  bottom: 1px;
  right: 150px;
  transform: translate(-31%, -25%);

  @media (min-width: 767px) and (max-width: 1365px) {
    bottom: 0px;
    right: 0px;
    transform: translate(-5%, -15%);
  }

  @media screen and (min-width: 1366px) {
    bottom: 0px;
    right: 0px;
    transform: translate(10%, -10%);
  }
}
.hero__button {
  font-family: $primary_FontFamily;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.16;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px solid transparent;

  color: var(--all-white-color);
  background-color: var(--all-orange-color);

  padding: 14px 0;
  margin-top: 30px;
  margin-right: 15px;
  margin-left: 15px;
  margin-bottom: 255px;
  width: 260px;

  @media (min-width: 767px) and (max-width: 1365px) {
    margin-left: 0;
    margin-right: 0;
    letter-spacing: 0.14rem;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1366px) {
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.14rem;

    margin-top: 109px;
    margin-bottom: 0;
    margin-right: 0;
    margin-left: 70px;
    padding: 27px 0;
    width: 354px;
  }
}
